import React, { useState } from "react";
import { HelmetProvider, Helmet } from "react-helmet-async";
import parse from "html-react-parser";

import Nav from "../nav";
import CursorOverlay from "./cursorOverlay";

export const SEO = ({ data }) => {
  if (data === undefined) return null;

  let headHTML = "";

  Object.keys(data).forEach(key => (headHTML += data[key]));

  return <Helmet defer={false}>{parse(headHTML)}</Helmet>;
};

export default ({ children, className, parentClass, seo }) => {
  const [cursorItems, setCursorItems] = useState([]);

  const onLoad = () => {
    document.querySelector("main").removeEventListener("onLoad", onLoad);

    let cursors = document.querySelectorAll(".hover-cursor");

    if (cursors.length <= cursorItems.length) return;

    setCursorItems(cursors);
  };

  return (
    <HelmetProvider>
      <SEO data={seo} />

      <main className={parentClass}>
        <Nav />

        <section onLoad={onLoad} className={className}>
          {children}
        </section>

        {cursorItems.length > 0 ? <CursorOverlay items={cursorItems} /> : null}
      </main>
    </HelmetProvider>
  );
};
